html {
    background: $background;
    font-size: calc(1.5vw + 10px);
	line-height: 1.5;
	width: 90%;
	margin: 0 auto;
}

h1  {
	font-family: 'Work Sans', sans-serif;
	font-size: 1.5rem;
    color: $main;
	margin: 10px 0;
	text-align: left;
}

h2  {
	font-family: 'Work Sans', sans-serif;
	font-size: 1.3rem;
	color: $text;
	margin: 0;
	
	span {
		text-transform: uppercase;
		color: $main;
	}
}

p {
    font-family: 'Source Sans Pro', sans-serif;
	color: $text;
	margin: 0;
	font-size: 0.8rem;
}

.tracker p {
	font-family: 'Work Sans', sans-serif;
}

select {
    background: $background;
    border: none;
	border-bottom: 5px solid $main;
	width: 100%;
    padding: 10px;
	color: $text;
	padding-left: 15px;

	/* Here's the code we need */
	-webkit-appearance: none;
	-moz-appearance: none;
	-ms-appearance: none;
	 -o-appearance: none;
		appearance: none;
}

.select-container {
    position: relative;
}

.select-arrow {
    color: $text;
    right: 10px;
	padding-right: 5px;
    font-size: 1.3rem;
    position:absolute;
    display:block;
    z-index: 5;
    margin: 0;
    pointer-events:none;
}

button {
	font-family: 'Source Sans Pro', sans-serif;
}

header {
	display: flex;
	justify-content: space-between;
	align-items: center;

	button {
		height: 70%;
		border: 3px solid $main;
		border-radius: 10px;
		color: $text;
		padding: 10px 20px;
		background: $background;
		font-size: 0.8rem;

		&:hover {
			background: $main;
			color: $background;
		}

		&:focus {
			background: $main;
			color: $background;
		}
	}
}

.instructions {
	width: 60%;
	margin: 0 auto;
	padding: 15px 0;
	font-size: 0.7rem;
}

.goals {
	width: 70%;
	margin: 0 auto;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);	
	z-index: 10;

	label {
		color: $background;
		font-size: 1rem;
		font-family: 'Source Sans Pro', sans-serif;
	}

	input {
		border: none;
		margin-bottom: 10px;
		border-top: 5px solid transparent;
		border-bottom: 5px solid $background;
		width: 60%;
		background: $main;
		padding: 10px;
		font-size: 0.8rem;
		text-align: center;
		color: $background;

		&::placeholder {
			color: $background;
			font-family: 'Source Sans Pro', sans-serif;
		}

		&:hover {
			border: 5px solid $background;
		}

		&:focus {
			border: 5px solid $background;
		}
	}

	select {
		width: 60%;
		margin: 0 auto 40px;
		// text-align: center;
		border-top: 5px solid transparent;
		border-bottom: 5px solid $background;
		background: $main;
		padding: 10px 10px 10px 30px;
		color: $background;
		font-size: 0.8rem;


		&:hover {
			border: 5px solid $background;
		}

		&:focus {
			border: 5px solid $background;
		}
		
	}

	.goal-select-arrow {
		color: $background;
		right: 150px;
		bottom: 55px;
		// padding-right: 5px;
		font-size: 1.5rem;
		position:absolute;
		display:block;
		z-index: 5;
		margin: 0;
		pointer-events:none;
	}

	button {
		border: none;
		padding: 10px;
		background: $background;
		color: $main;
		border-radius: 5px;
		// box-shadow: 5px 5px #208AAE;

		&:hover {
			background: $text;
			color: $background;
		}

		&:focus {
			background: $text;
			color: $background;
		}

	}

	fieldset {
		border: none;
		width: 65%;
		margin: 0 auto;
		background: $main;
		border-radius: 10px;
		box-shadow: 10px 10px $text;
	}
}

.flex-main {
	display: flex;
	align-items: center;
	margin: 30px 0;

	.tracker{
		width: 60%;
	}

	.results {
		width: 35%;
		min-height: 45vh;
		padding: 10px;
		border: 2px solid $main;
		border-radius: 5px;

		span {
			font-family: 'Work Sans', sans-serif;
			text-transform: uppercase;
			color: $main;
			font-size: 0.8rem;
		}

		p {
			font-size: 0.6rem;
		}

		button {
			border: none;
			margin: 10px 0;
			padding: 10px;
			background: $main;
			color: $background;
			border-radius: 5px;
			font-size: 0.6rem;
			// box-shadow: 5px 5px #208AAE;

			&:hover {
				background: $text;
				color: $background;
			}

			&:focus {
				background: $text;
				color: $background;
			}

		}
	}
}

@media (max-width: 850px) {
	.flex-main {
		flex-direction: column;

		.tracker {
			width: 90%;
		}

		.results {
			margin-top: 40px;
			width: 60%;
			min-height: 30vh;
		}
	}

	.goals {
		fieldset {
			width: 90%;
		}
	}
}

@media (max-width: 500px) {

	.goals {
		input, select {
			width: 80%;
			border-top: 3px solid transparent;
			border-bottom: 3px solid $background;
			margin: 10px 0;

			&:hover, :focus {
				border: 3px solid $background;
			}
		}

	}

	.instructions {
		width: 90%;
		font-size: 0.8rem;
	}
	.flex-main{

		margin: 5px;
		.results {
			width: 90%;
		}
	}

	select {
		padding-left: 5px;
	}

	.select-arrow {
		padding-right: 0;
		font-size: 1rem;
		margin-top: 5px;
	}

}

@media (max-width: 400px) {
	.select-arrow {
		display: none;
	}

	select {
		text-align: center;
		border-bottom: 3px solid $main;
	}
}